.GeneratorTitle {
    color: #282c34;
    font-weight: bold;
    font-size: 50px;
    padding: 20px;
}

.GeneratorOutput {
    padding: 50px;
    font-size: 50px;
    color: #282c34;
    background: white;
    text-decoration: underline;
    margin: 60px;
    outline: 4px solid #F28E2B;
}

.GeneratorButton {
    padding: 20px 40px;
    font-size: 30px;
    background: #282c34;
    color: white;
    outline: white 2px solid;
    display: flex;
}

.geninput {
  font-size: 40px;
  display: flex;
  flex-direction: center;
  text-align: center;
  margin: 50px 0px 0px 0px;
  color: #282c34;
}

.slider {
  -webkit-appearance: none;
  width: 100%;
  height: 25px;
  background: #d3d3d3;
  outline: none;
  opacity: 0.7;
  -webkit-transition: .2s;
  transition: opacity .2s;
  margin: 0px;
}

.slider:hover {
  opacity: 1;
}

.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 25px;
  height: 25px;
  background: #282c34;
  cursor: pointer;
}

.slider::-moz-range-thumb {
  width: 25px;
  height: 25px;
  background: #282c34;
  cursor: pointer;
}

.lengthText {
    color: #282c34;
    font-size: 20px;
    background: white;
}

select {
    overflow: hidden;
    width: 100%;
    height: 100%;
}