.infoTitle {
    font-size: 40px;
    background:#282c34;
    color: white;
    padding: 20px 20px;
    margin: 30px 0px;
    outline: grey 5px solid;
    cursor: pointer;
}

.infoDesc {
    padding: 35px 35px;
    align-items: center;
    justify-content: left;
    flex-direction: column;
    display: table-row;
    background-color: white;
    color: #282c34;
    margin: 0 auto;
    text-align: left;
    max-width: 800px;
    outline: 4px solid #F28E2B;
}

.infoCenter {
    align-items: center;
    justify-content: center;
    flex-direction: column;
    display: flex;
}